const app = getApp();
export default {
	data() {
		return {
			user_phone: ''
		};
	},
	onLoad() {
		if(app.reToken()) {
			uni.getStorageSync("access_token")
		}
		this.getUserInfo()
	},
	methods: {
		toBack() {
			uni.switchTab({
				url: './index'
			})
		},
		change_phone() {
			uni.navigateTo({
				url: './changePhone'
			})
		},
		change_password() {
			uni.navigateTo({
				url: './password'
			})
		},
		getUserInfo() {
			uni.request({
				url: getApp().globalData.url + 'api/user/info',
				method: 'GET',
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				success: (res) => {
					console.log(res)
					var data = res.data.items.user
					if(data.mobile) {
						var phone = data.mobile
						this.user_phone =  phone.replace(/^(\d{3})\d{4}(\d+)/,"$1****$2")
						uni.setStorageSync("phone_num", phone);
						uni.setStorageSync("user_phone", this.user_phone);
					}
					
				},
				fail: (res) => {
				
				}
			})
		},
	}
}
